<template>
    <div class="content">
      <Header :back="true">书籍详情</Header>
      <ul>
        <li>
          <label for="name">书籍名称</label>
          <input type="text" v-model="book.bookName" id="name">
        </li>
        <li>
          <label for="info">书籍信息</label>
          <input type="text" v-model="book.bookInfo" id="info">
        </li>
        <li>
          <label for="price">书籍价格</label>
          <input type="text" v-model.number="book.bookPrice" id="price">
        </li>
      </ul>
      <button @click="update">确认修改</button>
    </div>
</template>

<script>

  import Header from "../base/Header"
  import {findOneBook,updateBook} from "../api"

    export default {
        created() {
          this.getData();
        },
        data() {
            return {book: {}}
        },
        methods: {
          async update() {
            await updateBook(this.bid, this.book);
            this.$router.push("/list");
          },
          async getData() {
            this.book = await findOneBook(this.bid);
            Object.keys(this.book).length > 0 ? void 0 : this.$router.push("/list");
          }
        },
        computed: {
          bid() {
            return this.$route.params.bid;
          }
        },
        watch: {
          $route() {
            this.getData();
          }
        },
        components: {
          Header
        },
    }
</script>

<style scoped lang="less">
  .content{
    position: absolute;
    top: 40px;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
    background: #fff;
    ul{
      margin: 0 5px;
      li{
        margin: 10px 0;
        label{
          display: block;
        }
        input{
          height: 25px;
          width: 100%;
          outline: none;
          border: 1px solid #e1e1e1;
        }
      }
    }
    button{
      display: block;
      margin: 5px;
      width: 100px;
      height: 30px;
      line-height: 30px;
      border: none;
      outline: none;
      background: dodgerblue;
      color: #fff;
      border-radius: 10px;
      font-size: 16px;
    }
  }
</style>



